// Normally mainpage styles
body
	background: #FFF
%in
	margin: 0 auto
	width: 1200px
.btn
	button
		background-color: #38a8e8
		border: 2px solid #3799D1
		border-radius: 3px
		color: #FFF
		&:hover
			background-color: #2D7CAB
			border: none
	.btn__go
		margin-left: 775px 
		width: 70px
		height: 23px
	.btn__more
		float: right
		width: 96px
		height: 27px
		margin-top: 22px
		margin-right: 26px
.top
	@extend %in
	padding-top: 28px
	padding-bottom: 38px 
	.top__autofiles
		padding-bottom: 65px
		::-webkit-input-placeholder
			font-size: 12px
			font-family: arial
			font-style: italic
			color: #E5E5E5
		input[type="text"]
			color: #35A7E8
			font-style: normal
			padding-left: 25px  
		i
			display: inline-block
			+s(autofiles)
			margin-right: 135px
			float: left
		input
			width: 408px
			height: 37px 
			border: 1px solid #E5E5E5
			border-radius: 2px
			float: left
		button
			width: 92px
			height: 37px 
			margin-right: 200px 
			background-color: #35A7E8 
			border: 1px solid #3799D1
			border-radius: 2px
			float: left
			&:hover
				background: #2C7BAA
		.top__autofiles-search
			display: inline-block
			+s(search)
			margin-top: 3px 
			margin-right: 2px
		.top__autofiles-join
			margin-top: 8px 
			float: left
			margin-right: 33px  
			a
				color: #2FA4E7
				font-size: 18px
				font-weight: 300
				text-decoration: none
				&:hover
					text-decoration: underline 
			i
				display: inline-block
				+s(join)
				margin-top: 2px 
				margin-right: 8px 
		.top__autofiles-signin
			@extend .top__autofiles-join
			margin-right: 0 
			a
				color: #4D4D4D
	.top__list
		height: 38px
		background-color: #3FABE9
		border: 1px solid #2E80B0
		border-radius: 2px
		list-style: none
		li
			display: inline-block 
		a 
			height: 36px 
			line-height: 36px 
			text-align: center
			text-decoration: none
			color: #FFF
			border-right: 1px solid #2E80B0 
			border-left: 1px solid #6BC9F2 
			font-family: Arial
			font-size: 14px
			padding: 0 19px
			margin-right: -3.5px
			letter-spacing: 0.7px 
			display: inline-block
			&:hover
				background: #2C7BAA
.main
	@extend %in
	padding-bottom: 2920px 
	.main__left
		float: left
		width: 870px 
		.all-makes
			padding-bottom: 272px 
			h1
				color: #2FA4E7
				font-size: 18px 
				font-weight: 700
			.all-makes__list
				width: 145px 
				float: left
				margin-top: 25px
				a
					height: 30px 
					line-height: 30px 
					color: #4D4D4D
					font-size: 14px
					font-family: Arial
					text-decoration: none
					&:hover
						text-decoration: underline 
		.main__select-options
			@extend .all-makes
			padding-bottom: 45px
			.main__select-options_form
				margin-top: 15px
				width: 100%
				height: 40px 
				line-height: 40px 
				background-color: #F5F5F5 
				border: 1px solid #E3E3E3 
				border-radius: 2px
				.main__select-options_year
					float: left
					margin-left: 22px 
					p
						float: left
						margin-right: 20px 
						color: #4D4D4D 
						font-family: Arial 
						font-size: 14px
						font-weight: 700
					select
						float: left
						margin-top: 8px 
						padding-left: 10px  
						width: 120px 
						height: 24px 
						line-height: 24px
						background-color: #FFF
						border: 1px solid #CCC
						border-radius: 3px 0 0 3px
						color: #4D4D4D
						font-size: 12px
						font-family: Arial
					button
						width: 22px
						height: 24px
						background-color: #FFF
						border-top: 1px solid #CCC
						border-bottom: 1px solid #CCC
						border-right: 1px solid #CCC
						border-radius: 0 3px 3px 0
					i
						display: inline-block
						+s(arrow)
						margin-bottom: 1px
					
				.main__select-options_make
					@extend .main__select-options_year 
				.main__select-options_model
					@extend .main__select-options_year
		.main__accessories
			@extend .all-makes
			padding-bottom: 1000px
			border-bottom: 1px solid #e3e3e3
			h1
				margin-bottom: 35px 
			.main__accessories-type
				width: 290px
				float: left
				margin-bottom: 40px 
				h2
					color: #2FA4E7 
					font-size: 14px
					font-weight: 700 
					margin-top: 13px 
					margin-bottom: 10px
				a
					text-decoration: none
					color: #4D4D4D
					font-size: 12px
					font-family: Arial
					line-height: 20px
					&:hover
						color: #2FA4E7
				button
					width: 31px
					height: 22px
					padding-bottom: 5px  
					margin-left: 10px  
					background-color: #36A7E8
					border: 1px solid #3798D0
					border-radius: 2px
					color: #FFF
					font-family: Arial
					font-size: 11px
					font-weight: 700 
					letter-spacing: 0.7px 
					text-align: center
					vertical-align: center
					&:hover
						background-color: #2D7CAB
				.main__accessories-type_btn1
					top: 0
					position: absolute
					margin-top: 915px
					margin-left: 85px
				.main__accessories-type_btn2
					@extend .main__accessories-type_btn1
					margin-left: 119px
				.main__accessories-type_btn3
					@extend .main__accessories-type_btn1
					margin-left: 104px 
				.main__accessories-type_btn4
					@extend .main__accessories-type_btn1
					margin-top: 1240px 
				.main__accessories-type_btn5
					@extend .main__accessories-type_btn2
					margin-top: 1240px
				.main__accessories-type_btn6
					@extend .main__accessories-type_btn3
					margin-top: 1240px
				.main__accessories-type_btn7
					@extend .main__accessories-type_btn1
					margin-top: 1566px
				.main__accessories-type_btn8
					@extend .main__accessories-type_btn2
					margin-top: 1566px
				.main__accessories-type_btn9
					@extend .main__accessories-type_btn3
					margin-top: 1566px
				.main__accessories-type_open
					display: none
					a
						text-decoration: none
						color: #4D4D4D
						font-size: 12px
						font-family: Arial
						line-height: 20px
						&:hover
							color: #2FA4E7
			.main__accessories-type_left
				float: left	     
		.main__most-visited-models
			@extend .main__accessories
			padding-bottom: 185px
			border-bottom: none 
			h1
				margin-top: 45px
			ul
				float: left
				width: 290px
			a
				color: #4D4D4D
				font-size: 12px
				font-family: Arial
				line-height: 23px
				text-decoration: none
				&:hover
					color: #2FA4E7
		.main__dirtopia
			margin: auto
			width: 728px
			height: 90px    			 
		.main__car-reviews
			@extend .all-makes
			padding-bottom: 45px
			h1
				margin-top: 45px
				margin-bottom: 35px
			.main__car-reviews_list
				height: 37px
				list-style: none
				border-bottom: 1px solid #e3e3e3 
				li
					margin-right: 7px 
					display: inline-block 
				a 
					height: 37px 
					line-height: 37px 
					text-align: center
					border-radius: 3px
					text-decoration: none
					color: #FFF
					background-color: #4D4D4D  
					font-family: Arial
					font-size: 14px
					font-weight: 700 
					display: inline-block
					padding: 0 19px
					&:hover 
						background: #FFF
						color: #4D4D4D 
						border-top: 1px solid #E3E3E3
						border-left: 1px solid #E3E3E3
						border-right: 1px solid #E3E3E3
						border-bottom: none
						border-radius: 2px 
						line-height: 35px
						padding: 0 18px    
			.main__car-reviews_tipes
				padding-bottom: 360px
				border-radius: 2px
				border-bottom: 1px solid #e3e3e3
				border-left: 1px solid #e3e3e3 
				border-right: 1px solid #e3e3e3
				.main__car-reviews_model
					float: left
					width: 280px
					padding-top: 25px 
					padding-left: 25px
					h2
						color: #2FA4E7 
						font-size: 14px
						font-weight: 700 
						margin-top: 13px 
						margin-bottom: 10px
					p
						color: #4D4D4D
						font-family: Arial
						font-size: 12px
		.main__automotive-news
			@extend .all-makes
			padding-bottom: 50px
			h1
				margin-bottom: 35px 
			.main__automotive-news_list
				padding-top: 24px 
				padding-bottom: 73px
				border: 1px solid #E3E3E3
				border-radius: 2px
				.main__automotive-news_one
					display: inline-block
				.main__automotive-news_concept1
					width: 815px 
					height: 50px
					border-bottom: 1px solid #e3e3e3
					margin: 0 24px
					display: block
					a
						height: 50px
						border-bottom: 1px solid #e3e3e3
						text-decoration: none   
					h2
						padding-top: 2px 
						color: #2FA4E7 
						font-size: 14px
						font-weight: 700
					.main__automotive-news_date
						color: #CCC
						font-family: Arial
						font-size: 11px
						padding-top: 7px
				.main__automotive-news_detail1
					display: none
					margin: 0 24px
					img
						float: left
						margin-right: 23px
						margin-bottom: 23px      
					h2
						padding-top: 2px 
						padding-bottom: 13px 
						color: #2FA4E7 
						font-size: 14px
						font-weight: 700
					.main__automotive-news_detail-date
						color: #CCC
						font-family: Arial
						font-size: 11px
					.main__automotive-news_detail-discription
						color: #4D4D4D
						font-family: Arial
						font-size: 12px
				.main__automotive-news_concept2
					@extend .main__automotive-news_concept1
				.main__automotive-news_detail2
					@extend .main__automotive-news_detail1
				.main__automotive-news_concept3
					@extend .main__automotive-news_concept1
				.main__automotive-news_detail3
					@extend .main__automotive-news_detail1
				.main__automotive-news_concept4
					@extend .main__automotive-news_concept1
				.main__automotive-news_detail4
					@extend .main__automotive-news_detail1
				.main__automotive-news_concept5
					@extend .main__automotive-news_concept1
				.main__automotive-news_detail5
					@extend .main__automotive-news_detail1
				.main__automotive-news_concept6
					@extend .main__automotive-news_concept1
				.main__automotive-news_detail6
					@extend .main__automotive-news_detail1
	.main__right
		float: right
		width: 330px
		.main__banner
			float: right
			margin-top: 40px 
		.main__cars-specs
			width: 300px 
			float: right
			margin-top: 40px 
			padding: 18px 
			border: 1px solid #E3E3E3
			border-radius: 2px
			h1
				color: #2FA4E7
				font-size: 18px 
				font-weight: 700
				margin-bottom: 15px 
			a
				width: 264px
				height: 38px
				display: inline-block
				color: #4D4D4D
				font-family: Arial
				font-size: 12px
				text-decoration: none
				line-height: 45px  
				&:hover
					color: #000
			.main__time
				i
					display: inline-block
					+s(time)
					float: left
					margin-right: 30px
					margin-top: 10px
					margin-left: 10px   
			.main__engine
				@extend .main__time
				i
					+s(engine)
					margin-right: 23px 
			.main__horsepower
				@extend .main__engine
				i
					+s(horsepower)
			.main__gas
				@extend .main__time
				i
					+s(gas)
					margin-left: 16px 
			.main__towing
				@extend .main__engine
				i
					+s(towing)
					margin-left: 12px 
			.main__length
				@extend .main__time
				i
					+s(length)
					margin-left: 17px 
			.main__wheelbase
				@extend .main__time
				i
					+s(wheelbase)
					margin-left: 7px 
					margin-right: 18px 
			.main__clearance
				@extend .main__time
				i
					+s(clearance)
					margin-top: 3px
					margin-right: 25px  
			.main__curb
				@extend .main__time
				i
					+s(curb)
					margin-top: 7px 
					margin-left: 14px 
					margin-right: 28px 
			.main__cargo
				@extend .main__time
				i
					+s(cargo) 
					margin-top: 0
					margin-right: 21px 
		.main__tires
			@extend .main__cars-specs
			margin-top: 50px
			padding-bottom: 15px 
			h1
				margin-bottom: 20px 
			.main__tires_img
				width: 50% 
				margin-top: 3px 
				display: inline-block
				+s(tires)
				float: left
			.main__tires_list
				width: 50% 
				float: right
				a
					height: 23px
					line-height: 23px  
		.main__wallpapers
			@extend .main__tires
			a
				height: 27px 
				line-height: 27px 
.footer
	@extend %in
	overflow: hidden
	padding-bottom: 67px
	border-top: 1px solid #e3e3e3
	.footer__links
		float: left
		list-style: none
		ul
			line-height: 67px 
		li
			display: inline-block
		a
			color: #2FA4E7
			font-size: 12px
			font-family: Arial
			text-decoration: none 
			&:hover
				color: #2D7CAB 
		.footer__abous-us
			margin-left: 13px 
			margin-right: 20px
	.footer__copyright
		float: right
		line-height: 67px 
		color: #4D4D4D
		font-family: Arial
		font-size: 12px  